.owl-carousel {
	position: relative;
	.owl-nav {
		.owl-prev,
		.owl-next {
			width: 40px;
			position: absolute;
			top: 50%;
			cursor: pointer;
			color: inherit;
			&:hover {
				color: green;
			}
			&::before {
				font-family: "FontAwesome";
				font-size: 40px;
			}
		}
		.owl-prev {
			left: -40px;
			&::before {
				content: '\f104';
			}
		}
		.owl-next {
			right: -40px;
			&::before {
				content: '\f105';
			}
		}
	}
	.owl-dots {
		text-align: center;
		.owl-dot {
			display: inline-block;
			margin: 0 5px;
			span {
				display: block;
				width: 12px;
				height: 12px;
				border-radius: 50%;
				background: rgba(0,0,0,0.3);
				transition: opacity .3s ease;
			}
			&.active,
			&:hover {
				span {
					background: rgba(0,0,0,0.6);
				}
			}
		}
	}
}